<template>
  <div>
    <bread-nav :nav="nav"></bread-nav>
    <main-part>
      <div class="btn-block">
        <a download  class="f-small main-c mr5" href="../gy-files/template/异动生信息导入模板.xls" >下载模板</a>
        <label for="importDom">
          <div class="btn-main btn-upload ml5">批量导入</div>
        </label>
        <input accept=".csv, application/vnd.openxmlformats-officedocument.spreadsheetml.sheet, application/vnd.ms-excel"
               @change="upload($event)" ref="upload" id="importDom" type="file" hidden>
      </div>
      <search-box :searchData="searchData" :table="table"></search-box>
      <resize-table>
      <table class="table">
        <thead>
        <tr>
          <th width="8%">姓名</th>
          <th width="7%">性别</th>
          <th width="15%">转入学校</th>
          <th width="10%">异动学期</th>
          <th width="15%">异动类型</th>
          <th width="25%">异动原因</th>
          <th width="10%">办手续时间</th>
          <th width="10%">操作</th>
        </tr>
        </thead>
        <tbody>
        <tr v-for="item in table.list">
          <td>
            <span @click="$router.push('/edu-affair/move-student/' + item.id)" class="oper">{{item.name}}</span>
          </td>
          <td>{{$enums.Sex.getName(item.sex)}}</td>
          <td>{{item.rollTo}}</td>
          <td>{{item.termName}}</td>
          <td>{{$enums.TransactionType.getName(item.transactionType)}}</td>
          <td>{{item.transactionReason || '--'}}</td>
          <td>{{item.handleTime | substrTime}}</td>
          <td>
            <span @click="$router.push('/edu-affair/move-student/' + item.id)" class="oper">详情</span>
          </td>
        </tr></tbody>
      </table>
      </resize-table>
      <no-data v-show="!table.noData"></no-data>
      <paging :data.sync="table"></paging>
    </main-part>
  </div>
</template>
<script>
  export default {
    data() {
      return {
        nav: [{name: "异动生信息", path: ""}],
        searchData: [
          {type: 'select',title: '性别', model: 'sex', option: this.$enums.Sex.list},
          {type: 'select',title: '异动类型', model: 'transactionType', option: this.$enums.TransactionType.list},
          {type: 'input', placeholder: '姓名'}
        ],
        table:{
          url: '/studentTransaction/queryStudentTransactionPage.do',
          el: '.table',
          total: 0,
          noData: true,
          list: [],
          query: {
            sex: null,
            transactionType: null,
            keywords: null
          }
        }
      }
    },
    methods: {
      // 导入
      async upload(e) {
        if (e.target.files.length === 0) return
        let file = e.target.files[0]
        this.$refs.upload.value = null;
        let formData = new FormData()
        formData.append('file', file)
        let obj = {
          url: '/studentTransaction/importStudentTransactionList.do',
          query: formData,
          el: '.btn-upload',
          noText: true
        }
        // 上传模板拿到返回的模板地址
        let res = await this.$ajax(obj)
        if(res.errMsgList && res.errMsgList.length){
          this.$message.error(res.errMsgList[0])
        }else{
          this.$remind('导入成功')
        }
        this.$search(this.table)

      },
    },
    mounted(){
      this.$search(this.table)
    }
  }
</script>
<style lang="sass" scoped>
  .btn-block
    .main-c
      vertical-align: top
      display: inline-block
      line-height: 30px
      margin-top: 2px
</style>
